// This is only to be ready for Clarity v3, where the recommended
// way to implement custom clarity theme overrides is by using CSS
// Custom Variables. We have to switch to this as soon as we upgrade
// to Clarity v3, as SASS overrides are deprecated in Clarity v3
// and will not work in Clarity v4.
// Note: CSS custom vars are not supported by IE 11,
// so "runCssVarsPolyfill" helper Polyfill should be
// applied as stated in Clarity documentation:
// https://clarity.design/documentation/themes#examples
@if ($vsphere-css-custom-properties) {
   :root {
      --clr-table-bgcolor: #{$vsphere-neutral-row-bg-color};
      --clr-datagrid-row-hover: #{$vsphere-hover-row-bg-color};
      --clr-datagrid-row-selected: #{$vsphere-selected-row-text-color};
   }
}