// This is only to be ready for Clarity v3, where the recommended
// way to implement custom clarity theme overrides is by using CSS
// Custom Variables. We have to switch to this as soon as we upgrade
// to Clarity v3, as SASS overrides are deprecated in Clarity v3
// and will not work in Clarity v4.
// Note: CSS custom vars are not supported by IE 11,
// so "runCssVarsPolyfill" helper Polyfill should be
// applied as stated in Clarity documentation:
// https://clarity.design/documentation/themes#examples
@if ($vsphere-css-custom-properties) {
   :root {
      --clr-global-selection-color: #{$vsphere-selected-row-bg-color};
      --clr-global-hover-color: #{$vsphere-hover-row-bg-color};
      --clr-color-neutral-0: #{$vsphere-color-neutral-0};
      --clr-color-neutral-50: #{$vsphere-color-neutral-50};
      --clr-color-neutral-100: #{$vsphere-color-neutral-100};
      --clr-color-neutral-200: #{$vsphere-color-neutral-200};
      --clr-color-neutral-300: #{$vsphere-color-neutral-300};
      --clr-color-neutral-400: #{$vsphere-color-neutral-400};
      --clr-color-neutral-500: #{$vsphere-color-neutral-interactive-element}; // #999999 -> #565656
      --clr-color-neutral-600: #{$vsphere-color-neutral-800}; // #737373 -> #444
      --clr-color-neutral-700: #{$vsphere-color-neutral-700};
      --clr-color-neutral-800: #{$vsphere-color-neutral-800};
      --clr-color-neutral-900: #{$vsphere-color-neutral-900};
      --clr-color-neutral-1000: #{$vsphere-color-neutral-1000};
   }
}