// This is only to be ready for Clarity v3, where the recommended
// way to implement custom clarity theme overrides is by using CSS
// Custom Variables. We have to switch to this as soon as we upgrade
// to Clarity v3, as SASS overrides are deprecated in Clarity v3
// and will not work in Clarity v4.
// Note: CSS custom vars are not supported by IE 11,
// so "runCssVarsPolyfill" helper Polyfill should be
// applied as stated in Clarity documentation:
// https://clarity.design/documentation/themes#examples
@if ($vsphere-css-custom-properties) {
   :root {
      --clr-forms-border-color: #{$vsphere-neutral-row-forms-border-color};
      --clr-forms-select-caret-color: #{$vsphere-neutral-row-forms-icon-color};
      --clr-forms-select-caret-hover-color: #{$vsphere-neutral-row-forms-icon-hover-color};
      --clr-forms-checkbox-background-color: #{$vsphere-color-action-blue-500};
      --clr-forms-radio-focused-shadow: 0 0 0;
      --clr-forms-textarea-focused-outline: 0 0 0;
      --clr-forms-textarea-invalid-focused-outline: 0 0 0;
      --clr-forms-text-disabled-color: #{$vsphere-neutral-row-disabled-forms-text-color};
      --clr-forms-label-disabled-color: #{$vsphere-neutral-row-disabled-forms-label-color};
      --clr-forms-border-disabled-color: #{$vsphere-neutral-row-disabled-forms-border-color};
      --clr-forms-subtext-disabled-color: #{$vsphere-neutral-row-disabled-forms-subtext-color};
      --clr-forms-placeholder-color: #{$vsphere-neutral-row-forms-placeholder-color};
   }
}